<template>
  <div class="panel">
    <h3 class="panel-title">{{title}}</h3>
    <ul class="panel-list">
      <li class="panel-list-item" v-for="item in items" @click="getMusic(item.radioid)">
        <img v-lazy="item.picUrl" class="list-item-bg">
        
        <i class="list-item-play icon-play"></i>
        <p class="list-item-des">{{item.songListDesc}}</p>
        <h4 class="list-item-author">{{item.Ftitle}}</h4>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapMutations,mapActions} from "vuex"
import {getRadio} from 'api/song'
import {playMode} from "api/config"
import {ERR_OK} from "api/config"
import {getRadioList} from "common/js/music"

// import axios from "axios"
export default {
  name: "list",
  methods:{
getMusic(item){
  getRadio(item).then(res=>{
    console.log(res.data)
    if(res.code===ERR_OK){
      res.data.forEach(item=>{
        this.list.push(getRadioList(item));
      })
      this.selectPlay({list:this.list,index:0,fullScreen:true});


    }
  })
// this.setSet({})

},
  ...mapMutations({
      setSet:"SET_SET"
    }),
  ...mapActions([
    "selectPlay"
    ])
  },
 
 
  props: {
    title: {
      type: String,
      default: ""
    },
    items: {
      type: Array,
      default () {
        return [{
           Ftitle: 0,
           picUrl: "",
           radioid:"",
          }]
        }
      
    }
  },
  data(){
    return{
      list:[]
    }
  }

}

</script>
<style lang="less" type="text/less" scoped>
@import "~common/less/index"; 
.panel{
 margin: 0 20px 14px; 

  .panel-title{ font-size: font-size-medium-x; margin-bottom:22px; } 
  .panel-list{ display: flex; flex-flow: row wrap; 
    justify-content: space-between; 
    .panel-list-item{ 
      background-color: @color-background-l;
      width:48%; 
      margin-bottom: 20px;
      position: relative; 
      .list-item-bg{ display: block; width: 100%; 
        margin-bottom:10px;} 
      .list-item-accessnum{ 
        position: absolute; left: 10px; bottom:100px; color:@color-text-l; } 
        font-size: @font-size-small;
        .icon-erji{ 
font-size: @font-size-small;
padding-right:10px;
        }
        .list-item-play{ position: absolute; right: 10px; 
          font-size: @font-size-large-s; 
          bottom:60px; color:@color-text-l; } 
          .list-item-des{ 
            text-overflow: ellipsis; 
            white-space: nowrap; 
            overflow: hidden; 
            font-size: @font-size-medium; 
            padding: 0px 14px 10px;
          } 
          .list-item-author{ .list-item-des; font-size: @color-text-l; padding-top:0;} 
          } 
          } 
        }
</style>



